

body, h1, h2, p, ul, li, form {
  margin: 5;
  padding: 12px;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f8f8f8;
}

header.sticky {
  position: sticky;
  top: 0;
  background-color: #333;
  color: #fff;
  padding: 10px 0;
  text-align: center;
  z-index: 100;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin:0 20px;
}

nav ul li a {
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  font-size: x-large;
}

.animation {
  width: 50px;
  height: 50px;
  background-color: red;
  position: absolute;
  left: -60px;
  top: 50%;
  transform: translateY(-50%) rotate(0deg);
  animation: moveRotate 4s infinite;
  z-index: -1;
}

@keyframes moveRotate {
  0% {
      left: -60px;
      transform: translateY(-50%) rotate(0deg);
  }
  33% {
      left: calc(33% - 30px);
      transform: translateY(0%) rotate(180deg);
  }
  66% {
      left: calc(66% - 30px);
      transform: translateY(0%) rotate(360deg);
  }
  100% {
      left: calc(100% + 60px);
      transform: translateY(-50%) rotate(540deg);
  }
}

